export default class APIEndpoints
{
    static api_mount = "/api";

    static build_api = (endpoint) => {
        return APIEndpoints.api_mount + endpoint;
    };
    static get = {
        all: APIEndpoints.build_api("/get"),
        alias: (alias) =>  APIEndpoints.build_api(`/get/${alias}`),
    }
    static new = APIEndpoints.build_api("/new");
    static delete = (alias) => APIEndpoints.build_api(`/delete/${alias}`);
    static edit = (alias) => APIEndpoints.build_api(`/edit/${alias}`);
    static go = (alias) => window.location.origin + `/go/${alias}`;

}