import Box from '@mui/material/Box';
import {
    Button,
    Container,
    Grid,
    List,
    ListItem,
    ListItemButton,
    ListItemText,
    TextField,
    Pagination,
    Paper,
    IconButton,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle, Link, Typography
} from "@mui/material";
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import APIEndpoints from "./APIEndpoints";
import {useEffect, useState} from "react";
import debug from 'debug';

const Home = () => {
    const log = console.log;

    const [url, setUrl] = useState("");
    const [alias, setAlias] = useState("");
    const [aliasData, setAliasData] = useState([]);
    const [reRun, setReRun] = useState(false);
    const [searchTerm, setSearchTerm] = useState("");
    const [currentPage, setCurrentPage] = useState(1);
    const [openEditDialog, setOpenEditDialog] = useState(false);
    const [editAlias, setEditAlias] = useState(null);
    const [editUrl, setEditUrl] = useState("");
    const itemsPerPage = 8;


    const newAlias = (url, alias) => {
        log("Creating new alias", {url, alias});

        fetch(APIEndpoints.new, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({url, alias})
        })
            .then(response => response.json())
            .then(data => {
                log("New alias created successfully", data);
                setReRun(prev => !prev);
            })
            .catch(error => {
                log("Error creating new alias", error);
            });

        setUrl("");
        setAlias("");
    };

    const getAliases = () => {
        log("Fetching aliases");

        fetch(APIEndpoints.get.all)
            .then(response => response.json())
            .then(data => {
                log("Aliases fetched successfully", data);
                setAliasData(data.data.reverse());
            })
            .catch(error => {
                log("Error fetching aliases", error);
            });
    };

    const deleteAlias = (alias) => {
        log("Deleting alias", alias);

        fetch(APIEndpoints.delete(alias), {
            method: "GET",
        })
            .then(response => response.json())
            .then(data => {
                log("Alias deleted successfully", data);
                setReRun(prev => !prev);
            })
            .catch(error => {
                log("Error deleting alias", error);
            });
    };

    const handleEditAlias = () => {
        log("Editing alias", {editAlias, editUrl});

        fetch(APIEndpoints.edit(editAlias), {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({url: editUrl})
        })
            .then(response => response.json())
            .then(data => {
                log("Alias edited successfully", data);
                setReRun(prev => !prev);
                setOpenEditDialog(false);
            })
            .catch(error => {
                log("Error editing alias", error);
            });
    };

    useEffect(() => {
        getAliases();
    }, [reRun]);

    // Filter aliases based on search term
    const filteredData = aliasData.filter(aliasObj =>
        aliasObj.url.toLowerCase().includes(searchTerm.toLowerCase()) ||
        aliasObj.short_alias.toLowerCase().includes(searchTerm.toLowerCase())
    );

    // Calculate pagination
    const totalPages = Math.ceil(filteredData.length / itemsPerPage);
    const displayedData = filteredData.slice(
        (currentPage - 1) * itemsPerPage,
        currentPage * itemsPerPage
    );

    const handlePageChange = (event, value) => {
        setCurrentPage(value);
    };

    const handleNewAliasCreate = () => {
        newAlias(url, alias);
        setAlias("");
        setUrl("");
    };

    const openEditDialogHandler = (alias, url) => {
        setEditAlias(alias);
        setEditUrl(url);
        setOpenEditDialog(true);
    };

    return (
        <Container maxWidth="lg" sx={{p: 3}}>
            <Grid container spacing={2}>
                <Grid item md={8} xs={12}>
                    <TextField
                        label="Long Url"
                        variant="outlined"
                        fullWidth
                        value={url}
                        type="url"
                        onChange={(e) => setUrl(e.target.value)}
                    />
                </Grid>
                <Grid item xs={6} md={2}>
                    <TextField
                        label="Alias"
                        variant="outlined"
                        fullWidth
                        value={alias}
                        onChange={(e) => setAlias(e.target.value)}
                    />
                </Grid>
                <Grid item xs={6} md={2} sx={{display: "flex", justifyContent: "center"}}>
                    <Button
                        variant="contained"
                        fullWidth
                        onClick={() => handleNewAliasCreate()}
                    >
                        Shorten
                    </Button>
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        label="Search"
                        variant="outlined"
                        fullWidth
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                </Grid>
            </Grid>
            <List sx={{width: '100%', maxWidth: "100%"}}>
                {displayedData.map(aliasObj => (
                    <Paper sx={{m: 1}} key={aliasObj._id}>
                        <ListItem>

                            <ListItemText
                                primary={
                                    <Box sx={{display: "flex"}}>
                                        <Button
                                            variant="text"
                                            color={"primary"}
                                            href={APIEndpoints.go(aliasObj.short_alias)}
                                            target={"_blank"}
                                        >
                                            <Typography variant={"h6"}>
                                                {aliasObj.short_alias}
                                                <Typography variant={"caption"}>
                                                    <Box sx={{display: "flex", flexDirection: "column", p: 1}}>
                                                        <Link underline={"hover"} target="_blank">{aliasObj.url}</Link>
                                                        {/*<Box>{new Date(aliasObj.created_at).toLocaleString()}</Box>*/}
                                                    </Box>
                                                </Typography>
                                            </Typography>
                                        </Button>
                                    </Box>
                                }
                            />
                            <IconButton
                                sx={{ml: 1}}
                                onClick={() => openEditDialogHandler(aliasObj.short_alias, aliasObj.url)}
                            >
                                <EditIcon/>
                            </IconButton>
                            <IconButton
                                color={"error"}
                                sx={{ml: 1}}
                                onClick={() => deleteAlias(aliasObj.short_alias)}
                            >
                                <DeleteIcon/>
                            </IconButton>
                        </ListItem>
                    </Paper>
                ))}
            </List>
            <Pagination
                count={totalPages}
                page={currentPage}
                onChange={handlePageChange}
                sx={{display: 'flex', justifyContent: 'center', mt: 2}}
            />
            {/* Edit Dialog */}
            <Dialog open={openEditDialog} onClose={() => setOpenEditDialog(false)} fullWidth>
                <DialogTitle>Edit Alias</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Update the URL for alias: {editAlias}
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        label="URL"
                        type="url"
                        fullWidth
                        variant="outlined"
                        value={editUrl}
                        onChange={(e) => setEditUrl(e.target.value)}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setOpenEditDialog(false)}>Cancel</Button>
                    <Button onClick={() => handleEditAlias()} variant="contained">Save</Button>
                </DialogActions>
            </Dialog>
        </Container>
    );
};

export default Home;
